<template>
  <div id="app">
    <router-view></router-view>
    <webappfooter v-if="!($route.path == '/shopingcart')"></webappfooter>
    <div class="speciafuntion" v-if="$store.state.shopingnone">
    		<functionbtn :text="''" :backimg="'../static/img/a.png'"></functionbtn>
    		<functionbtn :text="''" :backimg="'../static/img/a.png'" @clickfunction="gotop"></functionbtn>
    </div>
  </div>
</template>

<script>
import webappfooter from './components/webappfooter'
import functionbtn from './components/functionbtn'
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const store = new Vuex.Store({
//	state   
		state:{
			text:"vuexapp.vue",
			caddress:"",
			paddress:"",
			area:"",
			maskshow:false,
			// shopingnone
			shopingnone:true,
			// 
			shopnum:0,
//			,1
			sign:0,
//			
			logged:false,
		},
//		getters state,
		getters:{
			
		},
//		mutations  state mutationsthis.$store.commit("add"),
		mutations:{
			changepaddress:function(state,paddress){
				state.paddress = paddress;
			},
			changecaddress:function(state,caddress){
				state.caddress = caddress;
			},
			changearea:function(state,area){
				state.area = area;
			},
			changemaskshow:function(state){
				state.maskshow = !state.maskshow;
			},
			// 
			changeshopingnone:function(state,boo){
				state.shopingnone = boo;
			},
			addshopnum:function(state,value){
				state.shopnum+=value;
			},
//			
			changesign:function(state){
				state.sign = state.sign?0:1;
			},
			changelogged:function(state){
				state.logged = true;
			}
		},
//		actions store
		actions: {
			
		}
})
export default {
  name: 'app',
  store,
  components: {
	webappfooter,
  	functionbtn,
  },
  methods:{
  	gotop(){
  		window.scrollTo(0,0)
  	}
  }
}
</script>

<style>
#app {
  font-family: '', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.speciafuntion {
	position: fixed;
	right: 6vw;
	bottom: 13vh;
}
</style>



